a,
a:visited,
a:focus {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  color: #fff;
}

.timeline {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60%;
  margin: 0 auto;
}

.step {
  display: flex;
  align-items: center;
  flex: 0 1 auto;
  position: relative;
  padding: 1.5rem;
  background-color: #03a9fc;
  height: 100px;
  cursor: default;
  -webkit-transition: background-color 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
  transition: background-color 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out, background-color 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out, background-color 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
  border-radius: 20px;
  margin: 4px;
  color: #000;
  -webkit-transform-origin: center center;
          transform-origin: center center;
  will-change: transform background-color;
}

.arrow {
  will-change: color;
  -webkit-transition: color 0.2s ease-in-out;
  transition: color 0.2s ease-in-out;
}

.step.true {
  background-color: gray;
}

.step.true + .arrow {
  color: gray;
}

.step:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

/* Composite */

.promoted {
  display: inline-block;
  margin: 4rem auto 0;
}

.promoted-video {
  position: relative;
  height: 20vh;
  will-change: transform;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transition: -webkit-transform 0.45s cubic-bezier(0.24, 0.47, 0.44, 0.93);
  transition: -webkit-transform 0.45s cubic-bezier(0.24, 0.47, 0.44, 0.93);
  transition: transform 0.45s cubic-bezier(0.24, 0.47, 0.44, 0.93);
  transition: transform 0.45s cubic-bezier(0.24, 0.47, 0.44, 0.93), -webkit-transform 0.45s cubic-bezier(0.24, 0.47, 0.44, 0.93);
}

.promoted:hover .promoted-video {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.cube__scene {
  width: 200px;
  height: 200px;
  margin: 0 auto;
  -webkit-perspective: 400px;
          perspective: 400px;
}

.cube {
  width: 100%;
  height: 100%;
  position: relative;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-animation: rotate 10s infinite linear;
          animation: rotate 10s infinite linear;
}

.cube__face {
  position: absolute;
  width: 200px;
  height: 200px;
}

.cube__face--front {
  background: hsla(0, 100%, 50%, 0.7);
}
.cube__face--right {
  background: hsla(60, 100%, 50%, 0.7);
}
.cube__face--back {
  background: hsla(120, 100%, 50%, 0.7);
}
.cube__face--left {
  background: hsla(180, 100%, 50%, 0.7);
}
.cube__face--top {
  background: hsla(240, 100%, 50%, 0.7);
}
.cube__face--bottom {
  background: hsla(300, 100%, 50%, 0.7);
}

.cube__face--front {
  -webkit-transform: rotateY(0deg) translateZ(100px);
          transform: rotateY(0deg) translateZ(100px);
}
.cube__face--right {
  -webkit-transform: rotateY(90deg) translateZ(100px);
          transform: rotateY(90deg) translateZ(100px);
}
.cube__face--back {
  -webkit-transform: rotateY(180deg) translateZ(100px);
          transform: rotateY(180deg) translateZ(100px);
}
.cube__face--left {
  -webkit-transform: rotateY(-90deg) translateZ(100px);
          transform: rotateY(-90deg) translateZ(100px);
}
.cube__face--top {
  -webkit-transform: rotateX(90deg) translateZ(100px);
          transform: rotateX(90deg) translateZ(100px);
}
.cube__face--bottom {
  -webkit-transform: rotateX(-90deg) translateZ(100px);
          transform: rotateX(-90deg) translateZ(100px);
}

@-webkit-keyframes rotate {
  from {
    -webkit-transform: rotate3d(1, 0, 0, 0);
            transform: rotate3d(1, 0, 0, 0);
  }
  to {
    -webkit-transform: rotate3d(1, 0, 0, 360deg);
            transform: rotate3d(1, 0, 0, 360deg);
  }
}

@keyframes rotate {
  from {
    -webkit-transform: rotate3d(1, 0, 0, 0);
            transform: rotate3d(1, 0, 0, 0);
  }
  to {
    -webkit-transform: rotate3d(1, 0, 0, 360deg);
            transform: rotate3d(1, 0, 0, 360deg);
  }
}

